import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/views/AboutView.vue')
  },
  {
    path: '/peritonealdialysis',
    name: 'PD',
    component: () => import('@/views/PeritonealView.vue')
  },
  {
    path: '/addperitonealdialysis',
    name: 'AddPD',
    component: () => import('@/views/AddPeritonealView.vue')
  },
  {
    path: '/question',
    name: 'question',
    component: () => import('@/views/QuestionView.vue')
  },
  {
    path: '/commonquestion',
    name: 'QA',
    component: () => import('@/views/QAView.vue')
  },
  {
    path: '/examination',
    name: 'examination',
    component: () => import('@/views/ExaminationView.vue')
  },
  {
    path: '/chart',
    name: 'chart',
    component: () => import('@/views/ChartView.vue')
  },
  {
    path: '/askquestion',
    name: 'ask',
    component: () => import('@/views/AskView.vue')
  },
  {
    path: '/teach',
    name: 'teach',
    component: () => import('@/views/TeachView.vue')
  },
  {
    path: '/medicine',
    name: 'medicine',
    component: () => import('../views/MedicineView.vue')
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
